import React, {Component} from 'react';
import logoImg from './jobs.png';
import './logo.css';

class Logo extends Component {
    render (){
        return (
            <div className="logo-container">
                <img src={logoImg} alt=""/>
            </div>);
    }
}

export default Logo;


